import { createCookieSessionStorage } from '@remix-run/cloudflare'

export const toastSessionStorage = createCookieSessionStorage({
	cookie: {
		name: 'en_toast',
		sameSite: 'lax',
		path: '/',
		httpOnly: true,
		secrets: process.env.SESSION_SECRET?.split(','),
		secure: process.env.NODE_ENV === 'production',
	},
})
